<template lang="pug">
#app
  .main-wrapper <!--  -->
    router-view(name="sidebar") <!-- bar -->
    TOPBAR(v-show="$route.name!= 'login'") <!-- bar -->
    div 
      router-view(name="filter", v-show="closeFilter") <!--  -->
    .page-wrapper <!--  -->
      .page-content(v-show="$route.name!= 'login'")
        .d-flex.justify-content-between.align-items-center.flex-wrap.grid-margin
          ul.dateChange(v-show="$route.name!= 'home'") <!--  -->
            label(
              @click="typeChang('q')",
              :class="{ 'date-active': dateType == 'q' }"
            ) 
            label(
              @click="typeChang('m')",
              :class="{ 'date-active': dateType == 'm' }"
            ) 
            label(
              @click="typeChang('')",
              :class="{ 'date-active': dateType == '' }"
            ) 
          .datepicker-group(v-show="$route.name!= 'home'") <!--  -->
            input.datepicker_box(
              type="date",
              v-model="date2",
              @change="dateChange"
            )
            input.datepicker_box(
              type="date",
              v-model="date1",
              @change="dateChange"
            )
            button.send-date(@click="sendDate") <!--  -->
              | 
              i.fas.fa-search.ml-1 
            .time-now.mt-2 <!--  -->
              span.time-title 
              i.fas.fa-long-arrow-alt-right.ml-2
              span.ml-2 {{ date2 }}
              span.ml-2 
              span.ml-2 {{ date1 }}
            li.mt-3 <!--   -->
              span.input-range (%)
              input.ml-2.input-range(
                type="range",
                name="points",
                min="0",
                max="99",
                value="-1",
                v-model="range",
                step="5",
                @change="dateChange"
              ) 
              span.ml-2 {{ rangeif }}

      transition(name="page", mode="out-in") <!--  -->
        keep-alive 
          router-view
</template>

<script>
import TOPBAR from "@/components/menu/topbar";
export default {
  name: "App",
  components: {
    TOPBAR,
  },
  data: function () {
    return {
      dateType: "", //
      date1: "", //
      date2: "", //
      range: 0, //  
    };
  },
  created() {
    let theDay = new Date(); // 
    let changeDay = 30; // 
    this.date1 = theDay.toISOString().substring(0, 10); //10
    let timeStamp = theDay.setDate(theDay.getDate() - changeDay); // theDay.getDate() 
    this.date2 = theDay.toISOString().substring(0, 10); //10
    let date1 = this.date1;
    let date2 = this.date2;
    let margin = this.rangeif;
    this.$store.dispatch("cros/getTime", { date1, date2, margin }); //  get
    this.$store.dispatch("fb/getTime", { date1, date2, margin }); //  get
    this.$store.dispatch("line/getTime", { date1, date2, margin }); //  get
  },
  computed: {
    closeFilter() {
      return this.$store.state.filter; // 
    },
    rangeif() {
      if (this.range == 95) {
        return 99;
      } else {
        return this.range;
      }
    },
  },
  methods: {
    typeChang(type) {
      this.dateType = type;
      this.$store.dispatch("cros/timeType", type); //
      this.$store.dispatch("fb/timeType", type);
      this.$store.dispatch("line/timeType", type);
    },
    dateChange() {
      let date1 = this.date1;
      let date2 = this.date2;
      let margin = this.rangeif;
      this.$store.dispatch("cros/getTime", { date1, date2, margin }); //
      this.$store.dispatch("fb/getTime", { date1, date2, margin });
      this.$store.dispatch("line/getTime", { date1, date2, margin });
    },
    sendDate() {
      if (this.$route.name.includes("cros")) {
        //cros
        this.$store.dispatch("cros/getData"); // cros api
        //this.$store.dispatch('cros/getDownload'); // cros api
      }
      if (this.$route.name.includes("fb")) {
        this.$store.dispatch("fb/getData"); // fb  aip

        // this.$store.dispatch('fb/getDownload'); // fb api
      }
      if (this.$route.name.includes("line")) {
        this.$store.dispatch("line/getData"); // line api
        //this.$store.dispatch('line/getDownload'); // line api
      }
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/all.scss"; //css
</style>



